.home_section{
    display: flex;
    align-items: center;
    justify-content: center;
}

.quote_h1{
    font-family: "Lexend";
    font-size: 48px;
    font-weight: 800;
    line-height: 60px;
    letter-spacing: 0em;
    text-align: center;
    color: #206EA7;
}

.quote_h4{
    font-family: "Lexend";
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    letter-spacing: 0.5em;
    text-align: center;
    color: rgba(0,0,0,1s);
}



.quotes_card_{
    font-family: "Lexend" !important;
    font-size: 60px;
    font-weight: 700;
    line-height: 75px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(32,110,167,1);
    margin-left: 90px;
    margin-right: 90px;
    margin-top: 290px;
}


.some_quotes{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 60px;
}


.some_quotes > h4 {
    font-family: "Lexend" !important;
    font-size: 20px;
    font-weight: 300;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;
    height: 170px;
    width: 1200px;
    border-radius: 0px;
}